import React from 'react'
import { useSelector } from 'react-redux'
import { List, Label, Message } from 'semantic-ui-react'

/* 
-- no data --
Cape Verde
Palestine
Saint Helena, Ascension and Tristan da Cunha
Guadeloupe
Saint Barthélemy
Curaçao
Myanmar
DR Congo
Åland Islands
French Guiana
Mayotte
Martinique
Ivory Coast
+1 not in table
*/
const CiaData = () => {
  const govData = useSelector((state) => state.countriesData.data?.Government)

  const name = govData['Country name']['conventional short form'].text

  const regNewLine = /; /g
  const regCapFirst = /\n\w/g
  const regUpper = /[A-Z]{2,}/gm
  const regBrackets1 = /\]\s?/g

  const transformText = (text) => {
    const text1 = text.replaceAll(regNewLine, ';\n\n')
    const text2 = text1.replaceAll(regCapFirst, (txt) =>
      txt.substr(0).toUpperCase()
    )
    const text3 = text2.replaceAll(
      regUpper,
      (txt) => txt.charAt(0) + txt.substr(1).toLowerCase()
    )
    const text4 = text3.replaceAll(regBrackets1, ']\n\n')
    return text4
  }

  const chiefOfState = transformText(govData['Executive branch']['chief of state'].text)

  const headOfGov = transformText(
    govData['Executive branch']['head of government'].text
  )

  const cabinet = govData['Executive branch']['cabinet'].text

  const elections = transformText(
    govData['Executive branch']['elections/appointments'].text
  )

  const electionResults = transformText(
    govData['Executive branch']['election results']?.text
  )

  const execNote = govData['Executive branch'].note

  const partiesText = !govData['Political parties and leaders'].text
    ? ''
    : transformText(govData['Political parties and leaders'].text)

  const partiesNote = !govData['Political parties and leaders'].note
    ? ''
    : transformText(govData['Political parties and leaders'].note)

  const execBranches = [
    { label: 'Chief of State', item: chiefOfState },
    { label: 'Head of Government', item: headOfGov },
    { label: 'Cabinet', item: cabinet },
    { label: 'Elections/Appointments', item: elections },
    { label: 'Election Results', item: electionResults },
    { label: 'Note', item: execNote },
  ]
console.log('partiesNote',partiesNote === '')
  return (
    <>
      <Label color="black">
        <h2>{name} </h2>{' '}
      </Label>

      <Label>
        <h3>{'Executive Branches of Government'} </h3>{' '}
      </Label>
      <List celled relaxed id="government">
        {execBranches.map((e, i) =>
          e.item === undefined ? (
            <React.Fragment key={i}></React.Fragment>
          ) : (
            <React.Fragment key={i}>
              <List.Item  >
                <List.Header>
                  <h3> {e.label}</h3>
                </List.Header>
              </List.Item>
              <List.Item id="preserve-whitespace">
                <p> {e.item}</p>
              </List.Item>
            </React.Fragment>
          )
        )}
      </List>
      {partiesText === undefined && partiesNote === undefined ? (
        <></>
      ) : (
        <>
          <Label>
            <h3>{'Political Parties and Leaders'} </h3>{' '}
          </Label>

          <List id="government">
            <List.Item id="preserve-whitespace">{partiesText}</List.Item>
            {partiesNote === '' ? (
              <></>
            ) : (
              <>
                <List.Item>
                  <Label color="black">{'Note'} </Label>
                </List.Item>
                <List.Item>{partiesNote}</List.Item>
              </>
            )}
          </List>
        </>
      )}
      <Message size="mini" color="black">
        This information was obtained from the CIA, free from copyright
        restrictions. Visit{' '}
        <a href="https://cia.gov/site-policies">the CIA public website</a> for
        details.
      </Message>
    </>
  )
}

export default CiaData
